import axios from "axios";
import {Message} from 'element-ui';
import router from '@/router'
function errorCreate(msg) {
  const error = new Error(msg)
  errorLog(error)
  throw error
}
// 
function errorLog(error) {
  // 
  Message({
      message: error,
      type: 'error',
      duration: 5 * 1000
  })
}

const service = axios.create({
  baseURL: 'https://api.github.com',
  timeout: 50000 // 
})

//  URL 
// service.defaults.baseURL = '';


// "Content-Type": "application/json",
// "Authorization": `token ${this.userConfigInfo.token}`
// }
// 
service.interceptors.request.use(config => {
  // this.$store.state.token
  // config.headers.Authorization = `token 48b3f0edc2a7bdb352ae6cd4b39aadfc26495597`;
  let token = localStorage.getItem('token')
  config.headers.Authorization = `token ${token}`;
  return config;
});

/// 
service.interceptors.response.use(
  response => {
      // dataAxios  axios  data
      const dataAxios = response.data
      // 
      const { code } = dataAxios
      //  code 
      if (code === undefined) {
          return dataAxios
      } else {
          //  code  
          switch (code) {
              case 200:
                  // [  ] code === 0 
                  return dataAxios.data
              case 400:
                  // [  ]  code
                  errorCreate(`[ code: 400 -  ] ${dataAxios.msg}: ${response.config.url}`)
                  //   errorLog(dataAxios.err)
                  break
              case 2001:
                  // 
                  localStorage.setItem('blogToken', dataAxios.data.token);
                  router.push('/back')
                  return true
              case 2002:
                  localStorage.removeItem('blogToken')
                  return false
              case 2003:
                  // [  ]  code  token 
                  router.push('/login')
                  errorCreate(`[ code: 2003 -  ] ${dataAxios.msg}: ${response.config.url}`)
                  break
              case 2004:
                  //  
                  router.push('/404')
                  // errorCreate(`[ code: 2004 -  ] ${dataAxios.msg}: ${response.config.url}`)
                  break
              default:
                  //  code
                  errorCreate(`${dataAxios.content}: ${response.config.url}`)
                  break
          }
      }
  },
  error => {
      if (error && error.response) {
          switch (error.response.status) {
              case 400:
                  error.message = '';
                  break
              case 401:
                  error.message = '';
                  break
              case 403:
                  error.message = '';
                  break
              case 404:
                  error.message = `: ${error.response.config.url}`;
                  break
              case 408:
                  error.message = '';
                  break
              case 500:
                  error.message = '';
                  break
              case 501:
                  error.message = '';
                  break
              case 502:
                  error.message = '';
                  break
              case 503:
                  error.message = '';
                  break
              case 504:
                  error.message = '';
                  break
              case 505:
                  error.message = 'HTTP';
                  break
              default:
                  break
          }
      }
      errorLog(error)
      return Promise.reject(error)
  }
)


export default service